export default {
	name:'xlist-input',
	methods:{
		bindName(e){
			let name = e.detail.value
			uni.setStorage({
				key:'orderName',
				data: name
			})
		},
		bindNum(e){
			let num = e.detail.value
			uni.setStorage({
				key:'orderNum',
				data: num
			})
		}
	}
}
